<template>
 <CRow>
      <CCol md="4">
        <CCard>
          <CCardHeader>
            <strong> Devices </strong>
          </CCardHeader>
          <CCardBody>
            <table class="table table-striped table-hover table-responsive-md">
              <thead>
                <th> Name </th> <th> Status </th> <th> Last Update </th>
              </thead>
              <tbody>
                <tr style="cursor:pointer;" class="table-success" v-for="item in devices" :key="item" @click="change(item)">
                  <td>  {{ item.name }} </td>
                  <td>  Online </td>
                  <td>  {{ item.lastupdate }} </td>
                </tr>
              </tbody>
            </table>
           <!-- <CListGroup accent>

              <CListGroupItem color="success" accent="success" style="cursor:pointer" v-for="item in devices" :key="item" @click="change(item)" >
              {{ item.name }}
              </CListGroupItem>
             
            </CListGroup> -->
          </CCardBody>
        </CCard>
        
          <CCard v-if="state">
            <CCardHeader>
            <strong> State </strong>
          </CCardHeader>
           <CCardBody>
           <table class="table table-responsive-md">
             <thead>
               <th> Attribite </th> <th> Value </th>
             </thead>
             <tbody>
               <tr>
                 <td> Time </td> <td> {{ state.devicetime }} </td>
               </tr>
               <tr>
                 <td> Latitude </td> <td> {{ state.latitude }} </td>
               </tr>
               <tr>
                 <td> Longitude </td> <td> {{ state.longitude }} </td>
               </tr>
               <tr>
                 <td> Valid </td> <td> {{ state.valid == 1? "True": " False" }} </td>
               </tr>
               <tr>
                 <td> Accuracy </td> <td> {{ state.accuracy }} km </td>
               </tr>
               <tr>
                 <td> Altitude </td> <td> {{ state.altitude }} </td>
               </tr>
             </tbody>
           </table>
         </CCardBody>
          </CCard>
      </CCol>
       <CCol md="8">
          <CCard accent-color="primary">
          <CCardHeader> Devices On Map </CCardHeader>
          <CCardBody>
 <gmap-map
    :center="center"
    :zoom="7"
    :map-type-id= "roadmap"
    style="width: 100%; height: 500px; position: absolute; left:0; top:0"
  >
    <gmap-marker
      :key="index"
       v-for="(m, index) in position"
      :position="m"
      :clickable="true"
      :draggable="true"
      :icon= "image"
      @click="center=m"
    ></gmap-marker>
  </gmap-map>
          </CCardBody>
        </CCard>
      
   </CCol>
 </CRow>
 
</template>

<script>

export default {
  name: 'Map',
  data () {
      return {
        roadmap:"roadmap",
        devices:[],
        center: {lat: 10.0, lng: 10.0},
        image:"/marker.png",
       position:[],
       state:'',
       loader:''
      }
    },
     mounted() {
       this.loader = this.$loading.show();
      //  this.position[0]=this.center
        this.getData()
    },
  methods: {
    getData(){
      this.axios.get("devices", {})
            .then(response => {
              this.devices = response.data.devices;
              this.loader.hide();
            })
    },
    change(pos){
      let arr=[];
       this.state = pos.positions[0];
      pos.positions;
      pos.positions.forEach(function(item){
        arr.push({ lat:item.latitude,lng:item.longitude })
})
     this.position = arr;
     console.log(this.position)
    //  this.center = {lat: lat,lng: lng};
    }
  }
}
</script>